<template>
  <div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs6>
          <v-list subheader>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>music_note</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.genre }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>title</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.title }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>person</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.artist }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>fast_forward</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.maxbpm == songData.minbpm ? songData.maxbpm : songData.minbpm + "-" + songData.maxbpm }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>stars</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>☆{{ songData.level }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <v-icon>assessment</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ songData.notes }} Notes&nbsp;/&nbsp;{{ songData.total }}&nbsp;total</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-flex>
        <v-flex xs6 v-if="videoId">
          <youtube :video-id="videoId" player-width="560" player-height="345"></youtube>
          <v-card v-if="accesstoken && refreshtoken">
            <v-card-text>
              <v-layout row>
                <v-flex p>
                  <v-switch
                    v-model="enableVideoIdEditor"
                    color="primary"
                  ></v-switch>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    :disabled="!enableVideoIdEditor"
                    v-model="videoId"
                  ></v-text-field>
                </v-flex>
                <v-flex p>
                  <v-btn 
                    :loading="updateVideoIdLoading"
                    :disabled="!enableVideoIdEditor || updateVideoIdLoading"
                    color="primary" 
                    @click.native="updateVideoId()">Update Video ID</v-btn>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs6 v-if="!videoId">
          <v-layout justify-center column fill-height>
            <v-card>
              <v-card-title primary-title>
                <div>
                  <h3 class="headline mb-0">Nothing Video</h3>
                  <div>Please add Youtube video.<span v-if="!accesstoken || !refreshtoken">(Need to login.)</span></div>
                </div>
              </v-card-title>
              <v-card-text v-if="accesstoken && refreshtoken">
                <v-layout row>
                  <v-flex p>
                    <v-switch
                      v-model="enableVideoIdEditor"
                      color="primary"
                    ></v-switch>
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field
                      :disabled="!enableVideoIdEditor"
                      v-model="videoId"
                    ></v-text-field>
                  </v-flex>
                  <v-flex p>
                    <v-btn 
                      :loading="updateVideoIdLoading"
                      :disabled="!enableVideoIdEditor ||updateVideoIdLoading"
                      color="primary" 
                      @click.native="updateVideoId()">Update Video ID</v-btn>
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-dialog v-model="progress" persistent max-width="290">
            <v-card>
              <v-card-text>
                <v-progress-linear indeterminate></v-progress-linear>
              </v-card-text>
            </v-card>
          </v-dialog>
          <div id="table"></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
/* eslint-disable */
import AWS from "aws-sdk";
import axios from "axios";

export default {
  name: "Song",
  watch: {
    $route(to, from) {
      this.getSongData();
    },
    accesstoken: function(newValue, oldValue) {
      this.$forceUpdate();
      this.enableVideoIdEditor = false;
      this.updateVideoIdLoading = false;
    },
    refreshtoken: function(newValue, oldValue) {
      this.$forceUpdate();
      this.enableVideoIdEditor = false;
      this.updateVideoIdLoading = false;
    },
    userid: function(newValue, oldValue) {
      this.$forceUpdate();
      this.enableVideoIdEditor = false;
      this.updateVideoIdLoading = false;
    }
  },
  props: {
    msg: String,
    accesstoken: String,
    refreshtoken: String,
    userid: String
  },
  mounted: function() {
    this.getSongData();
  },
  data() {
    return {
      table: null,
      progress: false,
      songData: {},
      videoId: "",
      enableVideoIdEditor: false,
      updateVideoIdLoading: false,
    };
  },
  methods: {
    getYoutubeId: function(songData) {
      this.videoId = songData.video ? songData.video.videoid : "";
    },
    getSongHash: function() {
      return this.$route.params.songhash;
    },
    getLnMode: function() {
      return this.$route.params.lnmode;
    },
    createTable: function(notes) {
      this.table = new Tabulator("#table", {
        layout: "fitColumns",
        rowFormatter: function(row) {
          if (row.getData().novalidate) {
            row.getElement().style.backgroundColor = "#A6A6DF";
          }
        },
        columns: [
          {
            formatter: "rownum",
            align: "center",
            width: 40,
            headerSort: false
          },
          {
            title: "Name",
            field: "username",
            width: 150,
            headerSort: false,
            formatter: function(cell, formatterParams) {
              let data = cell.getRow().getData();
              let userid = data.userid;
              return `<a href="./#/viewer/user/${userid}">${cell.getValue()}</a>`;
            }
          },
          {
            title: "Score",
            field: "score",
            align: "left",
            formatter: "progress",
            formatterParams: { min: 0, max: notes * 2, legend: true },
            headerSort: false
          },
          {
            title: "Combo",
            field: "combo",
            align: "left",
            formatter: "progress",
            formatterParams: {
              min: 0,
              max: notes,
              color: "orange",
              legend: true
            },
            headerSort: false
          },
          {
            title: "Clear",
            field: "clear",
            align: "center",
            width: 150,
            headerSort: false,
            formatter: function(cell, formatterParams) {
              switch (cell.getValue()) {
                case 0:
                  cell.getElement().style.backgroundColor =
                    "rgb(255, 255, 255)";
                  return "NoPlay";
                case 1:
                  cell.getElement().style.backgroundColor =
                    "rgb(192, 192, 192)";
                  return "Failed";
                case 2:
                  cell.getElement().style.backgroundColor =
                    "rgb(149, 149, 255)";
                  return "AssistEasy";
                case 3:
                  cell.getElement().style.backgroundColor =
                    "rgb(149, 149, 255)";
                  return "LightAssistEasy";
                case 4:
                  cell.getElement().style.backgroundColor =
                    "rgb(152, 251, 152)";
                  return "Easy";
                case 5:
                  cell.getElement().style.backgroundColor =
                    "rgb(152, 251, 152)";
                  return "Normal";
                case 6:
                  cell.getElement().style.backgroundColor = "rgb(255, 99, 71)";
                  return "Hard";
                case 7:
                  cell.getElement().style.backgroundColor = "rgb(255, 217, 0)";
                  return "ExHard";
                case 8:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "FullCombo";
                case 9:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "Perfect";
                case 10:
                  cell.getElement().style.backgroundColor = "rgb(255, 140, 0)";
                  return "Max";
              }
            }
          }
        ]
      });
    },
    getSongData: function() {
      this.progress = true;
      const songhash = this.getSongHash();
      const lnmode = this.getLnMode();

      const self = this;

      AWS.config.update({
        accessKeyId: "AKIAIUNY56NANW7JGMTA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "get_song_data",
        Payload: JSON.stringify({
          songhash: songhash + (lnmode ? "." + lnmode : ".0")
        })
      };

      lambda.invoke(params, function(err, data) {
        self.progress = false;
        if (!err) {
          const json = JSON.parse(data.Payload.toString());
          if (json.message == "success") {
            self.songData = json.SongData;
            self.createTable(json.SongData.notes);
            self.table.setData(json.IRDatas);
            self.getYoutubeId(json.SongData);
          }
        }
      });
    },
    updateVideoId: function() {
      this.updateVideoIdLoading = true;
      let AccessToken = this.accesstoken;
      let RefreshToken = this.refreshtoken;
      let Songhash = this.getSongHash();
      let VideoId = this.videoId;

      AWS.config.update({
        accessKeyId: "AKIAJGA7ZWB5TQNJMNCA",
        secretAccessKey: "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"
      });
      AWS.config.region = "us-east-1";
      let lambda = new AWS.Lambda();
      let params = {
        FunctionName: "update_song_data",
        Payload: JSON.stringify({
          AccessToken: AccessToken,
          RefreshToken: RefreshToken,
          Songhash: Songhash,
          VideoId: VideoId,
        })
      };

      const self = this;
      lambda.invoke(params, function(err, data) {
        self.updateVideoIdLoading = false;
        self.enableVideoIdEditor = false;
        self.updateVideoIdLoading = false;
        self.getSongData();
      });
    }
  }
};
</script>

